.main-table-container {
  display: grid;
  padding-top: 20px;
  align-items: center;
  justify-content: center;
}

.chart-container {
  width: 100%;
  height: auto;
}

.year-column {
  color: #1890ff;
  text-decoration: underline;
  cursor: pointer;
}
.year-column:hover {
  color: #40a9ff;
  text-decoration: none;
}
.graph-container {
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
